import React from "react";

function SearchForm(employeeSearch) {
  return (
    <form>
      <div className="form-group">
        <label htmlFor="search">Search:</label>
        <input
          onChange={employeeSearch.handleInputChange}
          value={employeeSearch.value}
          name="search"
          type="text"
          className="form-control"
          placeholder="Search For a Employee"
          id="search"
        />
        <br />
        <button onClick={employeeSearch.handleFormSubmit} className="btn btn-primary">
          Search
        </button>
      </div>
    </form>
  );
}

export default SearchForm;